<template>
  <div>
    <el-card>
      <div slot="header">
        基本信息
        <el-button
          style="float: right"
          type="info"
          size="small"
          @click="$router.go(-1)"
          >返回</el-button
        >
      </div>
      <div>
        <!-- :rules="rules" -->
        <el-form
          :model="Form"
          ref="formRef"
          :rules="rule"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="负责人" prop="pid" required>
                <el-select
                  v-model="Form.pid"
                  filterable
                  placeholder="请输入内容"
                >
                  <el-option
                    v-for="item in Options"
                    :key="item.id"
                    :label="item.teaName"
                    :value="item.teaId"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>

            <el-col :span="12">
              <el-form-item label="学科门类" prop="subjectCategory" required>
                <el-input v-model="Form.subjectCategory"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="著作权名称" prop="crName" required="">
                <el-input v-model="Form.crName"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="著作权编号" prop="crNumber"  required >
                <el-input v-model="Form.crNumber"></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="登记号" prop="registrationNumber" required>
                <el-input v-model="Form.registrationNumber"></el-input>
              </el-form-item>
            </el-col>

            <el-col :span="12">
              <el-form-item v-if="this.istea == 1" label="所属单位" prop="crBelongDept" required>
                <el-input v-model="Form.crBelongDept"></el-input>
              </el-form-item>
              <el-form-item v-else label="所属学院" prop="crBelongDept" required>
                <el-input v-model="Form.crBelongDept"></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="著作权类型" prop="crType" required>
                <el-input v-model="Form.crType"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="出版日期" prop="crPublishTime" required>
                <el-input v-model="Form.crPublishTime"></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="一级学科" prop="firstLevelDiscipline" required>
                <el-input v-model="Form.firstLevelDiscipline"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="备注" prop="remarksInfo">
                <el-input v-model="Form.remarksInfo"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
      </div>
    </el-card>

    <el-card>
      <div slot="header">作者信息
        <!-- 新增 -->
        <span>
          <el-tooltip 
                effect="dark"
                content="新增"
                placement="right"
                :enterable="false"
              >
                
                <el-button
                  class="add"
                  type="primary"
                  icon="el-icon-plus"
              v-if="is_authority.is_user_add"
                  size="mini"
                  @click="authAdd"
                ></el-button>
          </el-tooltip>
        </span>
          
              
      </div>
      <div>
        <el-table :data="authForm" border stripe>

          <el-table-column
            prop="authorType"
            label="作者类型"
            align="center"
          >
          <template slot-scope="scope">
              <el-select v-model="scope.row.authorType">
                <el-option
                  label="本校老师"
                  value="本校老师">
                </el-option>
                <el-option
                  label="本校学生"
                  value="本校学生">
                </el-option>
                <el-option
                  label="外校人员"
                  value="外校人员">
                </el-option>
              </el-select>
            </template>
          </el-table-column>

          <el-table-column prop="authorName" label="* 作者姓名" align="center">
            <template slot-scope="scope">
              <el-input v-model="scope.row.authorName"></el-input>
            </template>
          </el-table-column>

          <el-table-column
          width="150"
            prop="authorId"
            label="作者编号"
            align="center"
          >
          <template slot-scope="scope">
              <el-input v-model="scope.row.authorId"></el-input>
            </template>
            </el-table-column>

          <el-table-column
            width="80"  
            prop="authorSex"
            label="性别"
            align="center"
          >
          <template slot-scope="scope">
              <el-input v-model="scope.row.authorSex"></el-input>
            </template>
            </el-table-column>

          <el-table-column prop="authorEducation" label="学历" align="center">
            <template slot-scope="scope">
              <el-select v-model="scope.row.authorEducation" placeholder="请选择学历">
                  <el-option
                    label="博士研究生"
                    value="博士研究生">
                  </el-option>
                  <el-option
                    label="硕士研究生"
                    value="硕士研究生">
                  </el-option>
                  <el-option
                    label="本科"
                    value="本科">
                  </el-option>
                  <el-option
                    label="专科"
                    value="专科">
                  </el-option>
                  <el-option
                    label="高中及其以下"
                    value="高中及其以下">
                  </el-option>
                  <el-option
                    label="其他"
                    value="其他">
                  </el-option>
                </el-select>
            </template>
          </el-table-column>

          <el-table-column width="100" prop="signingOrder" label="* 署名顺序" align="center">
            <template slot-scope="scope">
              <el-input v-model="authForm[scope.$index].signingOrder"></el-input>
            </template>
          </el-table-column>

          <el-table-column v-if="this.istea == 1" prop="authorTitle" label="职称" align="center">
            <template slot-scope="scope">
              <el-input v-model="scope.row.authorTitle"></el-input>
            </template>
          </el-table-column>

          <el-table-column
          v-if="this.istea == 1"
            prop="authorWorkUnit  "
            label="工作单位"
            align="center"
          >
            <template slot-scope="scope">
              <el-input v-model="scope.row.authorWorkUnit"></el-input>
            </template>
          </el-table-column>

          <el-table-column
            prop="contributionRate"
            label="贡献率：（ ）单位：%"
            align="center"
            ><template slot-scope="scope">
              <el-input
                v-model="scope.row.contributionRate"
              ></el-input> </template
          ></el-table-column>

          <el-table-column label="操作" width="120px" align="center">
            <template slot-scope="scope">
              <!-- 删除 -->
              <el-tooltip
                class="item"
                effect="dark"
                content="删除"  v-if="is_authority.is_user_del"
                placement="top-start"
                :enterable="false"
              >
                
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  size="mini"
                  @click="delRow(scope, authForm)"
                ></el-button>
              </el-tooltip>
              
            </template>
          </el-table-column>
        </el-table>
      </div>
      <el-button class="save" type="primary" @click="submitForm"
        >保存
      </el-button>
    </el-card>

  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  data() {
    return {
      is_authority: {
        is_user_del: false,
        is_user_upt: false,
        is_user_add: false,
        is_user_add_role: false
      },
      rule:{
        pid: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        subjectCategory: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        crName: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        crNumber: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        crBelongDept: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        registrationNumber: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        crType: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        crPublishTime: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        firstLevelDiscipline: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        firstLevelDiscipline: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ]
      },
      accessory: [],
      Options: [],
      Form: {},
      authForm: [],
      List: [],
      istea: undefined,
    }
  },
  created() {
    this.isAuthority()
    this.getDetail()
    this.getTreeselect()
  },
  computed: {
    ...mapState(['btus']),
  },
  methods: {
    isAuthority() {
      var arr = this.btus.split(',')
      for (var key in this.is_authority) {
        if (arr.indexOf(key) != -1) {
          this.is_authority[key] = true
        }
      }
    },

    authAdd() {
      this.authForm.splice(this.authForm.length,0,{
        authorEducation: "",
        authorId: "",
        authorName: "",
        authorSex: "",
        authorTitle: "",
        authorType: "",
        authorWorkUnit: undefined,
        contributionRate: "",
        id: "",
        signingOrder: undefined,
        crId: this.$route.query.id,
      })
    },
    delRow(scope, rows) {
      // console.log(scope);
      if (scope.row.id) {
        const userid = scope.row.id.split(',')
        console.log(userid)
        this.$confirm('确认要删除吗?', '警告', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
          .then(async () => {
            await this.$http.delete(
              '/copyRightAuthorInfo/removeByIdCopyRightAuthorInfos',
              {
                data: userid
              }
            )
            // console.log(res);
            this.getDetail()
            this.Form = []
          })
          .then(() => {
            this.$message({
              showClose: true,
              message: '删除成功',
              type: 'success'
            })
          })
      } else {
        rows.splice(scope.$index, 1)
      }
    },

    async getTreeselect() {
      this.istea = this.$route.query.istea
      const id = this.$route.query.id
      // console.log(istea);
      if (this.istea == 1) {
        const { data: res } = await this.$http.post(
          '/teaBaseInfo/queryTeaBaseInfoList',
          {
            page: {
              current: 1,
              size: 999
            }
          }
        )
        this.Options = res.data.dataList.records
        if (res.code != 20000) return this.$message.error(res.message)
        // console.log(this.Options)
        // console.log(this.majorOptions)
      } else if (this.istea == 0) {
        const { data: res } = await this.$http.post(
          '/studentInfo/queryStudentInfoList',
          {
            page: {
              current: 1,
              size: 999
            }
          }
        )
        let a = res.data.dataList.records
        a = JSON.parse(JSON.stringify(a).replace(/stuId/g, 'teaId'))
        this.Options = JSON.parse(
          JSON.stringify(a).replace(/stuName/g, 'teaName')
        )
        if (res.code != 20000) return this.$message.error(res.message)
        // console.log(this.Options)
        // console.log(this.majorOptions)
      }
    },

    async getDetail() {
    this.istea = this.$route.query.istea
      if (!this.$route.query.id) {
        return
      }
      const id = this.$route.query.id
      // console.log(id)
      const { data: res } = await this.$http.get(
        '/copyRightInfo/getByIdCopyRightInfo/' + id
      )
      if (res.code != 20000) return this.$message.error(res.message)
      this.Form = res.data.entity
      console.log(this.Form)
      this.getAutherinfo()
    },
    async getAutherinfo() {
      const { data: res } = await this.$http.get(
        '/copyRightAuthorInfo/queryCopyRightAuthorInfoLists/' +
          this.Form.id
      )
      if (res.code != 20000) return this.$message.error(res.message)
      this.authForm = res.data.dataList
      console.log(this.authForm)
    },
    submitForm() {
      if (this.istea == 1) this.Form.isStu = 1
      else if (this.istea == 0) this.Form.isStu = 0
      this.$refs.formRef.validate(async valid => {
        if (valid) {
          for (let index = 0; index < this.authForm.length; index++) {
            if (!this.authForm[index].authorName  || !this.authForm[index].signingOrder) {
              return this.$message.error('请输入作者姓名和作者署名')
            }

          }
          await this.$http.post(
            '/copyRightInfo/saveOrUpdateCopyRightInfo',
            this.Form
          )

          if (!this.$route.query.id && this.istea == 1) {
              const { data: res } = await this.$http.post(
              '/copyRightInfo/queryTeaCopyRightInfoList' ,
               {
                  page: {
                    current: 1,
                    size: 999
                  }
                }
              )
              var Id = res.data.dataList.records.pop().id
              // console.log(Id)
          }else if(!this.$route.query.id && this.istea == 0){
            const { data: res } = await this.$http.post(
              '/copyRightInfo/queryStuCopyRightInfoList' ,
               {
                  page: {
                    current: 1,
                    size: 999
                  }
                }
              )
              var Id = res.data.dataList.records.pop().id
          }

          for (let index = 0; index < this.authForm.length; index++) {
            if (!this.$route.query.id) {
              this.authForm[index].crId = Id
              this.authForm[index].signingOrder = 1
            };
            var { data: res1 } = await this.$http.post(
              '/copyRightAuthorInfo/saveOrUpdateCopyRightAuthorInfo',
              this.authForm[index]
            )
          }

          if (res1.code != 20000) return this.$message.error(res1.message)
          if (res1.code == 20000) {
            this.$router.go(-1)
            return this.$message.success(res1.message)
            this.getDetail()
          }
        }
      })
    }
  }
}
</script>
<style lang="less" scoped>
.el-card {
  margin-bottom: 20px;
}
.save{
  display: block;
  margin: 20px auto;
  margin-bottom: 0;
  padding: 18px 30px;
}
.add{
  margin-left: 10px;
}
</style>
